import $ from "jquery";
function getUser() {
    var temp = localStorage.getItem("user");
    var user;
    if (temp != null) {
        return JSON.parse(temp);;
    } else {
        user = {
            name: '[Enter Name]',
            useOffice: true,
            useMedia: false,
            useGoogle: true,
            profile: ''
        }
    }

    if (user.profile == '' || user.name == '[Enter Name]' || user.profile == 'https://eu.ui-avatars.com/api/?rounded=true&name=[Enter Name]')
        user.profile = 'https://eu.ui-avatars.com/api/?rounded=true&name=' + user.name;

    if (user.useBackgroundImage == null)
        user.useBackgroundImage = true;
        
    if (user.backgroundImage == null){
        user.backgroundImage = "data:image/jpeg;base64,"
    }
    
    return user;
}

function setUser(user) { 
    user = JSON.stringify(user);
    localStorage.setItem("user", user);
 }



function getTheme() {
    var temp = localStorage.getItem("theme");

    var theme = JSON.parse(temp);

    if (theme != null) {
        return theme;
    } else {
        theme = {
            primary: "#097c8d",
            background: "#082649",
            font: "#f2ebc1",
            shadow: {
                "x": 1,
                y: 1,
                blur: 16,
                spread: -6,
                colour: "#0f0f0f"
            },
            widgetBackground: "#082649",
            borderRadius: 8,
            fontWeight: 300,
            backgroundOpacity: 80
        }
    }

    return theme;
}

function setTheme(theme){
    
    if(theme == null)
        theme = getTheme();

    var shadow = `${theme.shadow.x}px ${theme.shadow.y}px ${theme.shadow.blur}px ${theme.shadow.spread}px ${theme.shadow.colour}`

    $(':root').css({
        '--primary': theme.primary,
        '--background': theme.background,
        '--widget-background': theme.widgetBackground,
        '--font': theme.font,
        '--font-weight': theme.fontWeight,
        '--border-radius': theme.borderRadius + 'px',
        '--shadow': shadow,
        '--background-opacity': theme.backgroundOpacity + "%"
    });

    theme = JSON.stringify(theme);
    localStorage.setItem("theme", theme);
}

function getCSS() {
    var css = localStorage.getItem("css");

    if (css != null) {
        return css;
    } else {
        return '/*  Warning for advanced users with knowledge of HTML & CSS  */';
    }

}

function setCSS(css){
    localStorage.setItem("css", css);

    $("#customCSS").text(css);
}

function getCookie(cname) {
    let name = cname + "=";
    let decodedCookie = decodeURIComponent(document.cookie);
    let ca = decodedCookie.split(';');
    for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == ' ') {
            c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
        }
    }
    return "";
}

export {getUser, setUser, getTheme, setTheme, getCSS, setCSS, getCookie}